import { changeElementStyle } from '../../../../utils'
import { GenerateColorString } from '../types'

export const changeRootVar = (varName: string, newValue: string): void =>
  changeElementStyle(document.documentElement, { [varName]: newValue })

export const generateColorString: GenerateColorString = (opacity = 1, shrink) =>
  `rgba(247, 247, 247, ${opacity}) ${shrink}%`

export const updateGradients = (
  leftOpacity: number,
  rightOpacity: number,
): void => {
  const gradientLeftStart = generateColorString(leftOpacity, 0)
  const gradientLeftEnd = generateColorString(leftOpacity, 3)
  const gradientRightEnd = generateColorString(rightOpacity, 97)
  const gradientRightStart = generateColorString(rightOpacity, 100)

  changeRootVar('--tabs-gradient-left-start', gradientLeftStart)
  changeRootVar('--tabs-gradient-left-end', gradientLeftEnd)
  changeRootVar('--tabs-gradient-right-end', gradientRightEnd)
  changeRootVar('--tabs-gradient-right-start', gradientRightStart)
}
